body {
  background-color: #1b1a1a;
}

.container {
  max-width: 280px;
  outline: 1px solid red;
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
}

.weekly-trends {
  color: #fff;
  padding-left: 20px;
  padding-right: 20px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 500;
}

.wrapper-weekly-trends {
  justify-content: space-between;
  align-items: center;
  margin-top: 40px;
  margin-bottom: 24px;
  display: flex;
}

.title-weekly-trends {
  color: #fff;
}

.link-weekly-trends {
  color: #f87719;
}

h1, h2, h3, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  box-sizing: border-box;
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list-movie-card {
  flex-wrap: wrap;
  justify-content: center;
  row-gap: 16px;
  margin: 0;
  padding: 0;
  display: flex;
}

.item-movie-card {
  width: 280px;
  border-radius: 5px;
  position: relative;
  overflow: hidden;
}

.poster-movie-card {
  width: 100%;
}

.info-movie-card {
  z-index: 20;
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 12px;
  line-height: 1.6;
  position: absolute;
  bottom: 0;
  left: 0;
}

.title-movie-card {
  text-transform: uppercase;
  color: #fff;
  margin-top: 0;
  margin-bottom: 12px;
}

.overlay-movie-card {
  width: 100%;
  height: 100%;
  z-index: 10;
  background: linear-gradient(rgba(0, 0, 0, 0) 63.48%, #000 92.16%);
  position: absolute;
  top: 0;
  left: 0;
}

.wrapper-movie-card {
  color: #b7b7b7;
  justify-content: space-between;
  gap: 12px;
  margin-bottom: 16px;
  display: flex;
}

.genre-year-movie-card {
  align-items: center;
  display: flex;
}

.divider-movie-card {
  color: #b7b7b7;
  margin: 0 5px;
}

.genre-movie-card {
  max-width: 100px;
  white-space: nowrap;
  overflow: hidden;
}

.rating {
  align-items: flex-end;
  font-size: 15px;
  display: flex;
}

.rating-body {
  position: relative;
}

.rating-body:before {
  content: "★★★★★";
  display: block;
}

.rating-active {
  width: 0%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.rating-active:before {
  content: "★★★★★";
  width: 100%;
  height: 100%;
  color: #ff0;
  position: absolute;
  top: 0;
  left: 0;
}

.rating-items {
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.rating-item {
  height: 100%;
  opacity: 0;
  flex: 0 0 20%;
}

.rating-value {
  visibility: hidden;
  width: 0;
}

